          style="bottom: 120px; right: 10px;"
        >
          <v-icon color="grey darken-3">mdi-crosshairs-gps</v-icon>
        </v-btn>
      </div>

      <div class="map-search">
        <v-text-field
          v-model="keyword"
          placeholder="搜尋"
          solo
          append-icon="mdi-magnify"
          clearable
          hide-details
          @keyup.enter="searchText"
          @click:append="searchText"
          autocomplete="off"
          rounded
        ></v-text-field>
        <div class="mt-3">
          <v-btn
            @click="$router.push('/search/附近的餐廳')"
            small
