<script>
// import Map from './components/Map.vue'
import MapLoader from './components/MapLoader.vue'
import SearchEngine from './lib/search'

export default {
  name: 'App',

  components: { MapLoader },

  data: () => ({
    keyword: '',
    mapConfig: {
      zoom: 12,
      center: { lat: 24.79612, lng: 120.993 },
      fullscreenControl: false
    },
    adjustLat: 0,
    adjustLng: 0.008755
  }),
  computed: {
    SearchEngine() {
      return SearchEngine
    },
    markers() {
      if (this.SearchEngine.resultList.length === 0) {
        return []
      }
      return this.SearchEngine.resultList.map((v) => ({
        position: {
          lat: parseFloat(v.lat) + this.adjustLat,
          lng: parseFloat(v.lng) + this.adjustLng
        },
        id: v.cid
      }))
    }
  },
  methods: {
    searchText() {
      console.log('App:', this.keyword)
      this.$router.push(`/search/${this.keyword}`)
    },
    searchNearbyGPS() {
      console.log('search nearby gps')
      this.$router.push('/search/我附近的餐廳')
    }
  },
  mounted() {
    /*
    const happy = () => {
      this.markers.splice(0)
      this.markers.push({ lat: 35 + Math.random(), lng: 139 + Math.random() })
      this.markers.push({ lat: 35 + Math.random(), lng: 139 + Math.random() })
      this.markers.push({ lat: 35 + Math.random(), lng: 139 + Math.random() })
      this.markers.push({ lat: 35 + Math.random(), lng: 139 + Math.random() })

      setTimeout(() => {
        happy()
      }, 5 * 1000)
    }

    setTimeout(() => {
      happy()
    }, 5 * 1000)
    */
  }
}
</script>

