<script>
import SearchEngine from '../lib/search'
import axios from 'axios'
import Vue from 'vue'

export default {
  name: 'Detail',
  data: () => ({
    cid: '',
    placeInfo: {},
    aspectRatingsList: [
      { text: '食物', key: 'food' },
      { text: '服務', key: 'service' },
      { text: '氣氛', key: 'atmosphere' },
      { text: '清潔', key: 'cleanliness' },
      { text: '價值', key: 'value' }
    ]
  }),
  components: {},
  computed: {
    SearchEngine() {
      return SearchEngine
    },
    aspectRatingsDict() {
      let dict = {}
      this.aspectRatingsList.map((v) => (dict[v.key] = v.text))
      return dict
    }
  },
  methods: {
    load(cid) {
      // cid = '12930000113132718392'
      axios.get(`${window.APIBASE}/detail/${cid}`).then((res) => {
        console.log(res.data)
        Vue.set(this, 'placeInfo', res.data)
      })
    },
    aspectRatingDescription(placeInfo) {
      // 😄😫😐😡🤢
      /*
      function getRatingDescription(rating) {
        if (rating >= 4.5) {
          return '超讚'
        } else if (rating < 4.5 && rating >= 3.5) {
          return '良好'
        } else if (rating < 3.5 && rating >= 2.5) {
          return '普通'
        } else if (rating < 2.5 && rating >= 1.5) {
          return '不佳'
        } else if (rating < 1.5) {
          return '極差'
        }
      }
      */
      // 😄😐😡
      // ✔⚠️❌
      /*
mdi-check
mdi-check-all
mdi-close
mdi-close-thick
      */
      function getRatingDescription(rating) {
        if (rating >= 4.5) {
          return '✔✔'
        } else if (rating < 4.5 && rating >= 3.5) {
          return '✔'
        } else if (rating < 3.5 && rating >= 2.5) {
          return '⚠️'
        } else if (rating < 2.5 && rating >= 1.5) {
          return '❌'
        } else if (rating < 1.5) {
          return '❌❌'
        }
      }

      let aspectKeyRatingList = this.aspectRatingsList.map((v) => ({
        key: v.key,
        rating: placeInfo[`${v.key}_rating`],
        count: placeInfo[`${v.key}_count`]
      }))
      aspectKeyRatingList = aspectKeyRatingList.filter((v) => v.count >= 20)
      aspectKeyRatingList.sort((a, b) => b.rating - a.rating)

      if (aspectKeyRatingList.length === 0) {
        return ''
      } else if (aspectKeyRatingList.length === 1) {
        return (
          this.aspectRatingsDict[aspectKeyRatingList[0].key] +
          getRatingDescription(aspectKeyRatingList[0].rating)
        )
      } else {
        return (
          this.aspectRatingsDict[aspectKeyRatingList[0].key] +
          getRatingDescription(aspectKeyRatingList[0].rating) +
          ' · ' +
          this.aspectRatingsDict[
            aspectKeyRatingList[aspectKeyRatingList.length - 1].key
          ] +
          getRatingDescription(
            aspectKeyRatingList[aspectKeyRatingList.length - 1].rating
          )
        )
      }
    }
  },
  mounted() {
    // this.cid = this.$route.params.cid
    // this.load(this.cid)
  },
  created() {
    this.cid = this.$route.params.cid
    this.load(this.cid)
  },
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      if (vm.cid === to.params.cid) {
        return
      }
      vm.cid = to.params.cid
      vm.load(to.params.cid)
    })
  },
  beforeRouteUpdate(to, from, next) {
    console.log('beforeRouteUpdate!', to.params.cid)
    this.cid = to.params.cid
    this.load(to.params.cid)
    next()
  }
}
</script>

