<template>
  <div>
    <div id="map"></div>
    <!-- point 1 -->
    <template v-if="!!this.google && !!this.map">
      <!-- point 2 -->
      <map-provider :google="google" :map="map">
        <slot />
      </map-provider>
    </template>
  </div>
</template>

<script>
import GoogleMapsApiLoader from 'google-maps-api-loader'
import MapProvider from './MapProvider'
import SearchEngine from '../lib/search'

export default {
  props: {
    mapConfig: Object,
    apiKey: String,
    markers: Array
  },
  components: {
    MapProvider
  },
  watch: {
    markers(newVal) {
      this.removeAllMarkers()

      for (let val of newVal) {
        this.addMarker(val)
      }

      if (newVal.length > 0) {
        let lats = newVal.map((v) => v.position.lat)
        let lngs = newVal.map((v) => v.position.lng)
        let bound = {
          south: Math.min(...lats),
          west: Math.min(...lngs),
          north: Math.max(...lats),
          east: Math.max(...lngs)
        }
        this.fitBounds(bound)
      }
    }
  },
  data() {
    return {
      google: null,
      map: null,
      markersInst: []
    }
  },
  mounted() {
    GoogleMapsApiLoader({
      apiKey: this.apiKey
    }).then((google) => {
      this.google = google
      this.initializeMap()

      for (let val of this.markers) {
        this.addMarker(val)
      }
    })
  },
  methods: {
    initializeMap() {
      const mapContainer = this.$el.querySelector('#map')
      const { Map } = this.google.maps
      this.map = new Map(mapContainer, this.mapConfig)

      this.map.addListener('zoom_changed', () => {
        this.$emit('zoom-changed', this.map.getZoom())
      })

      let center = this.map.getCenter()
      SearchEngine.mapLat = center.lat()
      SearchEngine.mapLng = center.lng()

      this.map.addListener('mouseup', () => {
        let center = this.map.getCenter()
        this.$emit('center-changed', {
          lat: center.lat(),
          lng: center.lng()
        })
        SearchEngine.mapLat = center.lat()
        SearchEngine.mapLng = center.lng()
      })
    },

    removeAllMarkers() {
      for (let inst of this.markersInst) {
        inst.setMap(null)
      }
      this.markersInst.splice(0)
    },

    addMarker(args) {
      const { Marker } = this.google.maps
      let marker = new Marker({
        position: args.position,
        map: this.map,
        title: 'Child marker!!',
        id: args.id
      })

      marker.addListener('click', () => {
        this.$emit('marker-click', args.id)
        marker.setAnimation(this.google.maps.Animation.BOUNCE)
        marker.setAnimation(null)
        this.$router.push(`/detail/${args.id}`)
        // this.setCenter(args.position)
      })

      this.markersInst.push(marker)
    },

    setCenter(position) {
      this.map.setCenter(position)
      SearchEngine.mapLat = position.lat
      SearchEngine.mapLng = position.lng
    },

    fitBounds(bound) {
      this.map.fitBounds(bound, {
        left: this.$vuetify.breakpoint.mobile ? 20 : 450,
        bottom: 20,
        right: 20,
        top: 20
      })
    },

    setZoom(zoom) {
      this.map.setZoom(zoom)
    }
  }
}
</script>

<style scoped>
#map {
  height: 100vh;
  width: 100%;
}
</style>
